import { FieldState } from "../Logic/FieldState";
import { GameStatsInfo } from "../Logic/GameStatsInfo";
import { IState } from "../Save/SaveInterfaces";

interface IGameState
{
    gameStats: Array<GameStatsInfo>;
    field: FieldState;
    isFinished:boolean;
}

export class GameState extends IState
{
    gameStats:Array<GameStatsInfo>
    field:FieldState
    isFinished:boolean

    constructor(
        gameStats?:Array<GameStatsInfo>,
        field?:FieldState,
        isFinished?:boolean
    ) {
        super()

        this.gameStats = gameStats ?? new Array();
        this.field = field ?? {name: "name", field: new Array()};
        this.isFinished = isFinished ?? false
    }

    toJSON()
    {
        return {
            gameStats:this.gameStats,
            field:this.field,
            isFinished:this.isFinished
        }
    }

    static Load(input: IGameState): GameState {
        return new GameState(
            input.gameStats,
            input.field,
            input.isFinished
        );
    }
}
